.about-box {
    margin: 10% auto 0 auto;
    display: flex;
    height: 400px;
    max-width: 1000px;
    align-content: center;
    overflow-y: scroll;
}

.member-box {
    display: flex;
    flex-direction: column;
    margin: 20px;
}

.member-img {
    height: 200px;
    width: 200px;
    margin: 0 auto 35px auto;
    background-color: rgb(223, 223, 223);
    border-radius: 50%;
    border-style: none;
    display: inline-block;
    box-shadow: -3px 5px 8px 0 rgba(0, 0, 0, 0.2);
}

.member-img:hover {
    box-shadow: none;
}

.member-name {
    font-size: 25px;
    font-weight: 900;
}

.member-role {
    font-size: 15px;
    font-weight: 500;
}

.member-desc {
    width: 300px;
    margin: 40px auto 0 auto;
}


p, h1, h4, button{
    text-align: center;
}

.link {
    color:inherit;
}

.link:hover {
    color:inherit;
}
